import { InjectionToken } from '@angular/core';
import { LayoutPreset } from '../interfaces/layout-preset';

export const LAYOUT_PRESETS = new InjectionToken<LayoutPreset[]>('LAYOUT_PRESETS', {
  factory: () => [
    {
      alias: 'xs',
      columns: 4,
      defaultMargin: '16px',
      defaultGutter: '16px',
      defaultColumnSpan: 4
    },

    {
      alias: 'sm',
      columns: 8,
      defaultMargin: '24px',
      defaultGutter: '24px',
      defaultColumnSpan: 4
    },

    {
      alias: 'gt-sm',
      columns: 12,
      defaultMargin: '24px',
      defaultGutter: '24px',
      defaultColumnSpan: 4,
      maxWidth: '1280px'
    }
  ]
});
