import { Directive } from '@angular/core';
import { StyleDefinition } from '@angular/flex-layout/core';
import { LayoutDirective } from '../classes/layout-directive';

const selector: string = `
  [dgPlace],
  [dgPlace.xs], [dgPlace.sm], [dgPlace.md], [dgPlace.lg], [dgPlace.xl],
  [dgPlace.lt-sm], [dgPlace.lt-md], [dgPlace.lt-lg], [dgPlace.lt-xl],
  [dgPlace.gt-xs], [dgPlace.gt-sm], [dgPlace.gt-md], [dgPlace.gt-lg]
`;

const inputs: string[] = [
  'dgPlace',
  'dgPlace.xs', 'dgPlace.sm', 'dgPlace.md', 'dgPlace.lg', 'dgPlace.xl',
  'dgPlace.lt-sm', 'dgPlace.lt-md', 'dgPlace.lt-lg', 'dgPlace.lt-xl',
  'dgPlace.gt-xs', 'dgPlace.gt-sm', 'dgPlace.gt-md', 'dgPlace.gt-lg'
];

@Directive({
  selector,
  inputs
})
export class PlaceDirective extends LayoutDirective {
  public inputs: string[] = inputs;

  public buildStyle(inputValue?: string): StyleDefinition {
    return {
      'place-self': inputValue || 'auto'
    };
  }
}
