import { ElementRef, Inject, OnChanges, OnDestroy, OnInit, SimpleChanges } from '@angular/core';
import { MediaMarshaller, MediaObserver, StyleDefinition, StyleUtils } from '@angular/flex-layout/core';
import { BehaviorSubject, Observable, combineLatest } from 'rxjs';
import { map } from 'rxjs/operators';
import { LayoutPreset } from '../interfaces/layout-preset';
import { LAYOUT_PRESETS } from '../providers/layout-presets';

export abstract class LayoutDirective implements OnChanges, OnInit, OnDestroy {
  protected abstract inputs: string[];

  protected inputValue$: BehaviorSubject<string | undefined> = (() => {
    let subject$ = new BehaviorSubject<string | undefined>(undefined);

    this.$mediaMarshaller.init(
      this.nativeElement,
      this.directiveKey,

      (value) => {
        if (value !== subject$.value) {
          subject$.next(value);
        }
      }
    );

    return subject$;
  })();

  protected layoutPreset$: BehaviorSubject<LayoutPreset> = (() => {
    let subject$ = new BehaviorSubject<LayoutPreset>(this.defaultLayoutPreset);

    {
      let source$ = this.$mediaObserver.asObservable();

      source$.pipe(
        map(() => this.activeLayoutPreset || this.defaultLayoutPreset)
      ).subscribe(subject$);
    }

    return subject$
  })();

  protected styleChange$: Observable<StyleDefinition> = (() => {
    let source$ = combineLatest(this.inputValue$, this.layoutPreset$);

    return source$.pipe(
      map(([inputValue]) => this.buildStyle(inputValue))
    );
  })();

  protected get activeLayoutPreset(): LayoutPreset | undefined {
    return this.$layoutPresets.find(({ alias }) => this.$mediaObserver.isActive(alias));
  }

  protected get defaultLayoutPreset(): LayoutPreset {
    return this.$layoutPresets[0];
  }

  protected get nativeElement(): HTMLElement {
    return this.$elementRef.nativeElement;
  }

  protected get directiveKey(): string {
    return this.constructor.name;
  }

  public constructor(
    protected readonly $elementRef: ElementRef<HTMLElement>,
    protected readonly $mediaMarshaller: MediaMarshaller,
    protected readonly $mediaObserver: MediaObserver,
    protected readonly $styleUtils: StyleUtils,

    @Inject(LAYOUT_PRESETS)
    protected readonly $layoutPresets: LayoutPreset[]
  ) {}

  public ngOnChanges(changes: SimpleChanges): void {
    Object.entries(changes).forEach(([input, { currentValue }]) => {
      if (this.inputs.includes(input)) {
        this.$mediaMarshaller.setValue(
          this.nativeElement,
          this.directiveKey,
          currentValue,
          this.extractSuffix(input)
        );
      }
    });
  }

  public ngOnInit(): void {
    this.styleChange$.subscribe((style) => {
      this.$styleUtils.applyStyleToElement(this.nativeElement, style);
    });
  }

  public ngOnDestroy(): void {
    this.$mediaMarshaller.releaseElement(this.nativeElement);
  }

  protected abstract buildStyle(inputValue?: string): StyleDefinition;

  private extractSuffix(input: string): string {
    let result = /\.(xs|sm|md|lg|xl|lt-sm|lt-md|lt-lg|lt-xl|gt-xs|gt-sm|gt-md|gt-lg)$/.exec(input);

    if (result) {
      return result[1];
    }

    return '';
  }
}
