import { Directive } from '@angular/core';
import { StyleDefinition } from '@angular/flex-layout/core';
import { LayoutDirective } from '../classes/layout-directive';

const selector: string = `
  [dgContainer]
`;

const inputs: string[] = [
  // No inputs
];

@Directive({
  selector,
  inputs
})
export class ContainerDirective extends LayoutDirective {
  public inputs: string[] = inputs;

  public buildStyle(): StyleDefinition {
    let { maxWidth, defaultMargin } = this.layoutPreset$.value;

    return {
      'box-sizing': 'border-box',
      'max-width': maxWidth || null,
      'margin': maxWidth ? '0 auto' : null,
      'padding': defaultMargin
    };
  }
}
