import { Directive } from '@angular/core';
import { StyleDefinition } from '@angular/flex-layout/core';
import { LayoutDirective } from '../classes/layout-directive';

const selector: string = `
  [dgGrid]
`;

const inputs: string[] = [
  // No inputs
];

@Directive({
  selector,
  inputs
})
export class GridDirective extends LayoutDirective {
  public inputs: string[] = inputs;

  public buildStyle(): StyleDefinition {
    let { columns } = this.layoutPreset$.value;

    return {
      'display': 'grid',
      'grid-template-columns': `repeat(${columns}, 1fr)`
    };
  }
}
